"use client";

import React, { useState, useMemo } from "react";
import {
    Table,
    TableBody,
    TableCell,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import Badge from "@/components/ui/badge/Badge";
import { Modal } from "@/components/ui/modal";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import Image from "next/image";

type TravelType = "Flight" | "Train" | "Cab";
type ExpenseStatus = "Draft" | "Submitted" | "Approved" | "Rejected";

interface TravelExpense {
    id: number;
    tripName: string;
    travelType: TravelType;
    fromLocation: string;
    toLocation: string;
    travelDate: string;
    amount: number;
    status: ExpenseStatus;
    receipt?: string;
}

const initialData: TravelExpense[] = [
    {
        id: 1,
        tripName: "Client Visit",
        travelType: "Flight",
        fromLocation: "Bangalore",
        toLocation: "Mumbai",
        travelDate: "2025-08-10",
        amount: 4500,
        status: "Submitted",
        receipt: "/images/sample-receipt.png",
    },
];

export default function TravelExpensesPage() {
    const [data, setData] = useState<TravelExpense[]>(initialData);
    const [modalOpen, setModalOpen] = useState(false);
    const [mode, setMode] = useState<"create" | "edit" | "detail">("create");
    const [form, setForm] = useState<Partial<TravelExpense>>({});
    const [currentPage, setCurrentPage] = useState(1);
    const perPage = 5;

    const totalPages = Math.ceil(data.length / perPage);
    const displayedData = useMemo(() => {
        const start = (currentPage - 1) * perPage;
        return data.slice(start, start + perPage);
    }, [currentPage, data]);

    const openCreate = () => {
        setForm({
            tripName: "",
            travelType: "Flight",
            fromLocation: "",
            toLocation: "",
            travelDate: "",
            amount: 0,
            status: "Draft",
        });
        setMode("create");
        setModalOpen(true);
    };

    const openEdit = (item: TravelExpense) => {
        setForm({ ...item });
        setMode("edit");
        setModalOpen(true);
    };

    const openDetail = (item: TravelExpense) => {
        setForm({ ...item });
        setMode("detail");
        setModalOpen(true);
    };

    const handleSave = () => {
        if (!form.tripName || !form.travelDate) return alert("Fill required fields");

        if (mode === "edit") {
            setData(data.map((d) => (d.id === form.id ? { ...d, ...form } : d)));
        } else {
            const newItem: TravelExpense = {
                ...(form as TravelExpense),
                id: data.length + 1,
                status: "Draft",
            };
            setData([newItem, ...data]);
        }
        setModalOpen(false);
    };

    const handleSubmit = (id: number) => {
        setData(data.map((d) => (d.id === id ? { ...d, status: "Submitted" } : d)));
        setModalOpen(false);
    };

    const handleDelete = (id: number) => {
        if (confirm("Are you sure to delete this record?")) {
            setData(data.filter((d) => d.id !== id));
        }
    };

    const handleReceiptUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            const file = e.target.files[0];
            const reader = new FileReader();
            reader.onload = (ev) => setForm({ ...form, receipt: ev.target?.result as string });
            reader.readAsDataURL(file);
        }
    };

    return (
        <div className="p-6">
            <PageBreadcrumb pageTitle="Travel Expenses" />

            <div className="flex justify-end mb-4">
                <button
                    className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                    onClick={openCreate}
                >
                    New Travel Expense
                </button>
            </div>

            {/* Table */}
            <div className="overflow-hidden rounded-xl border border-gray-200 bg-white dark:border-white/[0.05] dark:bg-white/[0.03]">
                <div className="max-w-full overflow-x-auto">
                    <div className="min-w-[1102px]">
                        <Table>
                            <TableHeader className="border-b border-gray-100 dark:border-white/[0.05]">
                                <TableRow>
                                    <TableCell
                                        isHeader
                                        className="px-5 py-3 text-left font-medium text-gray-500"
                                    >
                                        Trip Name
                                    </TableCell>
                                    <TableCell
                                        isHeader
                                        className="px-5 py-3 text-left font-medium text-gray-500"
                                    >
                                        Type
                                    </TableCell>
                                    <TableCell
                                        isHeader
                                        className="px-5 py-3 text-left font-medium text-gray-500"
                                    >
                                        From
                                    </TableCell>
                                    <TableCell
                                        isHeader
                                        className="px-5 py-3 text-left font-medium text-gray-500"
                                    >
                                        To
                                    </TableCell>
                                    <TableCell
                                        isHeader
                                        className="px-5 py-3 text-left font-medium text-gray-500"
                                    >
                                        Date
                                    </TableCell>
                                    <TableCell
                                        isHeader
                                        className="px-5 py-3 text-left font-medium text-gray-500"
                                    >
                                        Amount
                                    </TableCell>
                                    <TableCell
                                        isHeader
                                        className="px-5 py-3 text-left font-medium text-gray-500"
                                    >
                                        Status
                                    </TableCell>
                                    <TableCell
                                        isHeader
                                        className="px-5 py-3 text-left font-medium text-gray-500"
                                    >
                                        Actions
                                    </TableCell>
                                </TableRow>
                            </TableHeader>
                            <TableBody className="divide-y divide-gray-100 dark:divide-white/[0.05]">
                                {displayedData.map((r) => (
                                    <TableRow key={r.id}>
                                        <TableCell className="px-5 py-4">
                                            <div
                                                className="text-blue-600 cursor-pointer hover:underline"
                                                onClick={() => openDetail(r)}
                                            >
                                                {r.tripName}
                                            </div>
                                        </TableCell>
                                        <TableCell className="px-5 py-4">{r.travelType}</TableCell>
                                        <TableCell className="px-5 py-4">{r.fromLocation}</TableCell>
                                        <TableCell className="px-5 py-4">{r.toLocation}</TableCell>
                                        <TableCell className="px-5 py-4">{r.travelDate}</TableCell>
                                        <TableCell className="px-5 py-4">{r.amount}</TableCell>
                                        <TableCell className="px-5 py-4">
                                            <Badge
                                                size="sm"
                                                color={
                                                    r.status === "Approved"
                                                        ? "success"
                                                        : r.status === "Submitted"
                                                            ? "warning"
                                                            : r.status === "Rejected"
                                                                ? "error"
                                                                : "primary"
                                                }
                                            >
                                                {r.status}
                                            </Badge>
                                        </TableCell>
                                        <TableCell className="px-5 py-4 flex gap-2">
                                            {r.status === "Draft" && (
                                                <>
                                                    <button
                                                        className="px-3 py-1 bg-blue-600 text-white rounded hover:bg-blue-700"
                                                        onClick={() => openEdit(r)}
                                                    >
                                                        Edit
                                                    </button>
                                                    <button
                                                        className="px-3 py-1 bg-green-600 text-white rounded hover:bg-green-700"
                                                        onClick={() => handleSubmit(r.id)}
                                                    >
                                                        Submit
                                                    </button>
                                                    <button
                                                        className="px-3 py-1 bg-red-600 text-white rounded hover:bg-red-700"
                                                        onClick={() => handleDelete(r.id)}
                                                    >
                                                        Delete
                                                    </button>
                                                </>
                                            )}
                                            {r.status === "Submitted" && (
                                                <button
                                                    className="px-3 py-1 bg-gray-400 text-white rounded cursor-not-allowed"
                                                    disabled
                                                >
                                                    Submitted
                                                </button>
                                            )}
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </div>

                {/* Pagination */}
                {totalPages > 1 && (
                    <div className="mt-4 flex justify-center gap-2">
                        <button
                            onClick={() => setCurrentPage(Math.max(currentPage - 1, 1))}
                            className="px-3 py-1 border rounded"
                            disabled={currentPage === 1}
                        >
                            Prev
                        </button>
                        {[...Array(totalPages)].map((_, idx) => (
                            <button
                                key={idx}
                                onClick={() => setCurrentPage(idx + 1)}
                                className={`px-3 py-1 border rounded ${currentPage === idx + 1 ? "bg-blue-600 text-white" : ""
                                    }`}
                            >
                                {idx + 1}
                            </button>
                        ))}
                        <button
                            onClick={() => setCurrentPage(Math.min(currentPage + 1, totalPages))}
                            className="px-3 py-1 border rounded"
                            disabled={currentPage === totalPages}
                        >
                            Next
                        </button>
                    </div>
                )}
            </div>

            {/* Modal */}
            <Modal isOpen={modalOpen} onClose={() => setModalOpen(false)} className="max-w-lg p-6 mx-auto">
                <h2 className="text-lg font-semibold mb-4">
                    {mode === "create" ? "New Travel Expense" : mode === "edit" ? "Edit Travel Expense" : "Travel Expense Details"}
                </h2>
                <div className="space-y-4">
                    <div>
                        <label className="block mb-1">Trip Name</label>
                        <input
                            type="text"
                            className="w-full border rounded px-3 py-2"
                            value={form.tripName || ""}
                            onChange={(e) => setForm({ ...form, tripName: e.target.value })}
                            disabled={mode === "detail"}
                        />
                    </div>
                    <div>
                        <label className="block mb-1">Travel Type</label>
                        <select
                            className="w-full border rounded px-3 py-2"
                            value={form.travelType || "Flight"}
                            onChange={(e) => setForm({ ...form, travelType: e.target.value as TravelType })}
                            disabled={mode === "detail"}
                        >
                            <option value="Flight">Flight</option>
                            <option value="Train">Train</option>
                            <option value="Cab">Cab</option>
                        </select>
                    </div>
                    <div>
                        <label className="block mb-1">From</label>
                        <input
                            type="text"
                            className="w-full border rounded px-3 py-2"
                            value={form.fromLocation || ""}
                            onChange={(e) => setForm({ ...form, fromLocation: e.target.value })}
                            disabled={mode === "detail"}
                        />
                    </div>
                    <div>
                        <label className="block mb-1">To</label>
                        <input
                            type="text"
                            className="w-full border rounded px-3 py-2"
                            value={form.toLocation || ""}
                            onChange={(e) => setForm({ ...form, toLocation: e.target.value })}
                            disabled={mode === "detail"}
                        />
                    </div>
                    <div>
                        <label className="block mb-1">Date</label>
                        <input
                            type="date"
                            className="w-full border rounded px-3 py-2"
                            value={form.travelDate || ""}
                            onChange={(e) => setForm({ ...form, travelDate: e.target.value })}
                            disabled={mode === "detail"}
                        />
                    </div>
                    <div>
                        <label className="block mb-1">Amount</label>
                        <input
                            type="number"
                            className="w-full border rounded px-3 py-2"
                            value={form.amount || 0}
                            onChange={(e) => setForm({ ...form, amount: Number(e.target.value) })}
                            disabled={mode === "detail"}
                        />
                    </div>
                    <div>
                        <label className="block mb-1">Receipt</label>
                        {mode !== "detail" && (
                            <input type="file" accept="image/*,application/pdf" onChange={handleReceiptUpload} />
                        )}
                        {form.receipt && (
                            <div className="mt-2">
                                <Image src={form.receipt} alt="Receipt" width={120} height={80} />
                            </div>
                        )}
                    </div>

                    {mode !== "detail" && (
                        <div className="flex justify-end gap-2">
                            <button
                                className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400"
                                onClick={() => setModalOpen(false)}
                            >
                                Cancel
                            </button>
                            <button
                                className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
                                onClick={handleSave}
                            >
                                {mode === "edit" ? "Update" : "Add"}
                            </button>
                        </div>
                    )}
                    {mode === "detail" && form.status === "Draft" && (
                        <button
                            className="px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700 w-full"
                            onClick={() => handleSubmit(form.id!)}
                        >
                            Submit Travel Expense
                        </button>
                    )}
                </div>
            </Modal>
        </div>
    );
}
